
const baseURL = 'http://192.168.1.157/yishuban2/app/index.php?i=2&c=entry&m=ewei_shopv2&do=mobile';   //
// const baseURL = 'http://112.25.69.93/api';  http://admin.chinaduancai.com/upload/  //
//  url:  param  way res
 /**
  *  @name 
  */
 async function apiPost(url,params){
 	if(!params)console.warn("params");
 	return await new Promise((resolve,reject)=>{
 		uni.request({
 			url:baseURL+url,
 			method :"POST",
 			header:{
 				// 'Token': token,
 				'Accept': 'application/json',
 				"content-type":"application/json"
 			},
 			data:params,
 			success(res) {
				if(res.data.resultCode==0){
					resolve(res.data)
				}else{
					uni.hideLoading()
					reject(res)
				}
 			},
 			fail(err) {
				uni.hideLoading()
 				reject(err.errMsg)
 			}
 		})
 	})
 }
 
 async function apiGet(url,params){
 	if(!params)console.warn("params");
 	return await new Promise((resolve,reject)=>{
 		uni.request({
 			url:baseURL+url,
 			method :"GET",
 			header:{
 				'Accept': 'application/json',
 				"content-type":"application/json"
 			},
 			timeout:6000,
 			data:params,
 			success(res) {
 				if(res.resultCode==0){
 					resolve(res.data)
 				}else{
					uni.hideLoading()
 					reject(res)
 				}
 			},
 			fail(err) {
				uni.hideLoading()
 				reject(err.errMsg)
 			}
 		})
 	})
 }
 
 
 
 // 
 async function login(params){
 	return await new Promise((resolve,reject)=>{
 		uni.request({
 			url:baseURL+'/login',
 			method :"GET",
 			header:{
 				// 'Token': token,
 				'Accept': 'application/json',
 				"content-type":"application/x-www-form-urlencoded"
 			},
 			data:params,
 			success(res) {
 				resolve(res.data)
 			},
 			fail(err) {
 				reject(err)
 			}
 		})
 	})
 }

export default {
    baseURL,
	apiPost,
	apiGet,
	login
}